import React, { useState, useEffect } from "react";
import { Button, Checkbox } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import AxioHook from 'axios-hooks'
import DataTable from 'react-data-table-component';
import { Container, Row, Col } from 'react-bootstrap';
import { getClients, postRolesForClient } from "../../crud/client.crud";
import { getRoles, deleteRole } from "../../crud/menu.crud";
import { CreateRoleModal } from "./CreateRoleModal";
import { DeleteWarning } from "./DeleteWarningModal";

export default function Clients() {
    const [currentToEdit, setCurrenRoleToEdit] = useState(false);
    const [loadingToggle, setLoadingToggle] = useState(false);
    const [showAlertWarning, setShowAlertWarning] = useState(false);

    const [displayCreateModa,setCreateDisplayCreate] = useState(false);
    const [{ data: clients, ...getClientsReq }, refetchClients] = AxioHook(getClients(), { useCache: false })
    const [rolesReq, fetchRoles] = AxioHook(getRoles(), { useCache: false })

    const columns = [
        {
            name: 'Role Name',
            selector: 'role',
            minWidth: "20%"
        },
        {
            name: 'Delete',
            compact: true,
            maxWidth: "3%",
            minWidth: "70px",
            style: { cursor: "pointer"},
            cell: (role) => {

                return (
                    <DeleteIcon
                    onClick={() => setShowAlertWarning(role)}
                        variant="warning"
                        
                    >{loadingToggle.id === role.id ? "Wait..." : "aa"}</DeleteIcon>
                )
            }
        }
    ];

    let Body = (
        <Col span={24}>
            <DataTable
                onRowClicked={(role) => setCurrenRoleToEdit(role)}
                progressPending={rolesReq.loading}
                subHeaderAlign="center"
                title="Roles"
                pagination={true}
                actions={<Button variant="contained" color="primary" onClick={() => {
                    setCreateDisplayCreate(true);
                }}>Create Role</Button>}
                columns={columns}
                data={rolesReq.data || []}
            />
        </Col>
    );

    if (getClientsReq.error) {
        Body = (
            <>
                <h3>Error fetching the resource</h3>
            </>
        );
    }

    return (
        <Container fluid>
            <Row>
                {Body}
            </Row>
            {displayCreateModa && <CreateRoleModal refetch={fetchRoles} handleClose={() => setCreateDisplayCreate(false)} />}
            {currentToEdit && <CreateRoleModal role={currentToEdit} refetch={fetchRoles} handleClose={() => setCurrenRoleToEdit(false)} />}
            {showAlertWarning && <DeleteWarning role={showAlertWarning} handleClose={() => {
                setShowAlertWarning(false)
                fetchRoles();
            }} />}
        </Container>
    );
}
