import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import MenuSection from "./MenuSection";
import MenuItemSeparator from "./MenuItemSeparator";
import MenuItem from "./MenuItem";
import * as builder from "../../ducks/builder";
import { getMenuItems } from "../../../../src/app/crud/menu.crud";
import AxioHook from 'axios-hooks'
import { CircularProgress } from "@material-ui/core";

const baseMenu = {
  header: {
    self: {},
    items: [
      {
        title: "Dashboards",
        root: true,
        alignment: "left",
        page: "dashboard",
        translate: "MENU.DASHBOARD"
      },
    ]
  },
  aside: {
    self: {},
  }
};

const MenuList = (props) => {
  const dispatch = useDispatch();
  const [menuSettedUp, setMenuSettedUp] = useState(false)
  const { currentUrl, menuConfig, layoutConfig, user } = props;

  const [getReq, doGet] = AxioHook(getMenuItems())

  useEffect(() => {
    if (getReq.data && menuSettedUp === false) {
      const dataToDispatch = baseMenu
      dataToDispatch.aside.items = getReq.data.map(menuItem => {
        return {
          title: menuItem.title,
          root: true,
          icon: "flaticon2-architecture-and-city",
          bullet: "dot",
          roles: menuItem.roleGuards.map(r => r.role),
          page: menuItem.page
        }
      })
      dispatch(builder.actions.setMenuConfig(dataToDispatch)); 
      setMenuSettedUp(true)
    }
  }, [getReq])

  let Body = (
    <div style={{ display: 'flex', justifyContent: 'center', height: '50vh', alignItems: 'center' }}>
      <CircularProgress />
    </div>
  )
  if (getReq.data) {
    Body = getReq.data.map(menuItem => {
      return {
        title: menuItem.title,
        root: true,
        icon: "flaticon2-architecture-and-city",
        bullet: "dot",
        roles: menuItem.roleGuards.map(r => r.role),
        page: menuItem.page
      }
    }).map((child, index) => {
      return (
        <>
          {child.section && <MenuSection item={child} />}
          {child.separator && <MenuItemSeparator item={child} />}
          {
            child.title && (
              <>
                {!child.roles && (
                  <MenuItem
                    key={index}
                    item={child}
                    currentUrl={currentUrl}
                    layoutConfig={layoutConfig}
                  />
                )}
                {child.roles && user && user.roleGuards.length !== 0 && user.roleGuards.some(p => child.roles.includes(p)) && (
                  <MenuItem
                    key={index}
                    item={child}
                    currentUrl={currentUrl}
                    layoutConfig={layoutConfig}
                  />
                )}
              </>
            )
          }
        </>
      );
    });
  } else if (getReq.error) {
    Body = <h3>Error loading menu</h3>
  }

  return (
    <React.Fragment>
      {Body}
    </React.Fragment>
  );
}

export default MenuList
