import React, { useState, useEffect } from "react";
import AxioHook from 'axios-hooks'
import { deleteRole } from "../../crud/menu.crud";
import { Dialog, DialogActions, DialogContentText, DialogTitle, DialogContent, Button} from "@material-ui/core";


export const DeleteWarning = ({role, handleClose}) => {
    const [deleteReq, doDelete] = AxioHook(deleteRole(), { manual: true })

    return (
        <Dialog
            open={true}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
        >
            <DialogTitle>
                {"Delete Role?"}
            </DialogTitle>
            <DialogContent>
                <DialogContentText>
                    Are you sure you want to delete {role.role} role?
                    All users associated with this role will be permanently delete
                      </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={() => {
                            doDelete({ data: { id: role.id  } })
                            .then(() => handleClose())
                        }} disabled={deleteReq.loading} color="primary">
                    Yes
                      </Button>
                <Button onClick={() => handleClose()} disabled={deleteReq.loading} color="secondary" color="primary" autoFocus>
                    No
                      </Button>
            </DialogActions>
        </Dialog>
    );
}