import React, { useState, useEffect } from 'react';
import { TextField, CircularProgress } from '@material-ui/core';
import clsx from "clsx";
import { Formik } from "formik";
import { getUserByToken } from "../../crud/auth.crud";
import { getParams, postParams } from "../../crud/url.crud";
import Notification from "../../widgets/Notification";
import AxioHook from 'axios-hooks'

const FormCreate = () => {
    const [loadingButtonStyle] = useState({
        paddingRight: "2.5rem"
    });
    const [user, setUser] = useState(null);

    const [{ data: fields, loading, error }, refetch] = AxioHook(getParams())
    const [{ data: postedData, loading: postingFields, error: errorPost }, postFieldData] = AxioHook(postParams(), { manual: true })

    useEffect(() => {
        getUserByToken()
            .then(r => setUser(r.data));
    }, []);

    const FormField = (fieldProps) => {
        return (
            <div className="form-group">
                <TextField
                    margin="normal"
                    label={fieldProps.label}
                    className="kt-width-full"
                    name={fieldProps.name}
                    placeholder={`The name for ${fieldProps.placeholder} we will send on your URL`}
                    onBlur={fieldProps.handleBlur}
                    onChange={fieldProps.handleChange}
                    value={fieldProps.values[fieldProps.name]}
                />
            </div>
        );
    }

    if (error) {
        // TODO: center messsage and extract as a widget
        return (<>
            <h3>{error.toString()}</h3>
            <button onClick={refetch}>Try again!</button>
        </>);
    }

    if (loading) {
        return (<>
            <h3>Fetching Data</h3>
            <CircularProgress />
        </>);
    }

    return (
        <>
            <Formik
                enableReinitialize={true}
                initialValues={fields.filter(f => "ourUrlParam" in f).reduce((prev, next) => {
                    prev[next.ourUrlParam] = next.customeUrlParam
                    return prev;
                }, { })}
                onSubmit={(values, { setStatus, setSubmitting }) => {
                    console.log(values)
                    postFieldData({ data: { urlParams: values } })
                    .then(() => setSubmitting(false))
                    .then(() => refetch())
                    .catch((err) => {
                        err.response && setStatus(err.response.data.error)
                        setSubmitting(false)
                    })
                }}
            >
                {(formProp) => (
                    <form
                        noValidate={true}
                        autoComplete="off"
                        className="kt-form"
                        onSubmit={formProp.handleSubmit}
                    >
                        {formProp.status ? (
                            <div role="alert" className="alert alert-danger">
                                <div className="alert-text">{formProp.status}</div>
                            </div>
                        ) : null}

                        {fields.filter(f => "ourUrlParam" in f).map(f => {
                            return (
                                <FormField key={f.ourUrlParam} {...formProp} value={formProp.values[f.ourUrlParam]} name={f.ourUrlParam} placeholder={f.ourUrlParam} label={f.property} handleChange={formProp.handleChange} onBlur={formProp.handleBlur} />
                            )
                        })}

                        {fields.filter(f => "name" in f).map(f => (
                            <FormField key={f.name} {...formProp} value={formProp.values[f.name]} name={f.name} placeholder={f.name} label={f.name} handleChange={formProp.handleChange} onBlur={formProp.handleBlur} />
                        ))}


                        <div className="kt-login__actions">

                            <button
                                id="kt_login_signin_submit"
                                type="submit"
                                disabled={formProp.isSubmitting}
                                className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                                    {
                                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": postingFields
                                    }
                                )}`}
                                style={loadingButtonStyle}
                            >
                                Save
                            </button>
                        </div>
                    </form>
                )}
            </Formik>
        </>
    );
}

export default FormCreate;