import React, { useState, useEffect } from "react";
import { Button, Typography, Paper, Grid } from '@material-ui/core';
import { clientIatacodes, postCodes } from "../../crud/iatacodes.crud";
import { getLocations } from "../../crud/locations.crud";
import FormCreate from "../../widgets/locationsMap/FormCreate";
import Notification from "../../widgets/Notification";
import AxioHook from 'axios-hooks'

export default function MyPage() {
    const [locations, setLocations] = useState(null);
    const [counter, setCounter] = useState(1);
    const [valuesToSend, setValuesToSend] = useState([]);
    const [createdLocations, setCreatedLocations] = useState([]);
    const [success, setSuccess] = useState(false);
    const [httpError, setHttpError] = useState(false);
    const [rolesReq, refetchLocation] = AxioHook(getLocations(), { useCache: false })

    useEffect(() => {
        if (rolesReq.data) setLocations(rolesReq.data)
    }, [rolesReq]);

    const [postCodeReq, doPostCode] = AxioHook(postCodes(), { manual: true })

    useEffect(() => {
        clientIatacodes()
            .then(r => {
                setCreatedLocations(r)
            })
            .catch(() => setHttpError(true))
    }, [])

    useEffect(() => {
        setValuesToSend([]);
    },[createdLocations]);

    if (!locations && !httpError) {
        return (
            <Paper>
                <Grid container>
                    <Grid item>
                        <Typography variant="h3" gutterBottom>
                            Loading...
                        </Typography>
                    </Grid>
                </Grid>
            </Paper >
        );
    }

    if (httpError) {
        return (
            <Paper>
                <Grid container direction="column">
                    <Grid item>
                        We failed to load the resource
                </Grid>
                    <Grid item>
                        <Button variant="contained" color="primary" onClick={() => { refetchLocation(); setHttpError(false) }}>
                            Try again
                        </Button>
                    </Grid>
                </Grid>
            </Paper>
        );
    }

    return (
        <Grid container direction="column" spacing={2}>
            {success && (
                <Notification msg="Codes saved on database!" />
            )}
            {postCodeReq.error && (
                <Notification msg={postCodeReq.error ? postCodeReq.error.toString() : "Could saved the codes!"} severity={'error'} />
            )}
            <Grid item container direction="column">
                    {createdLocations.map((c, idx) => {
                        return (
                                <FormCreate
                                    key={idx}
                                    iataMapCode={c}
                                    onDelete={() => {
                                        return clientIatacodes()
                                            .then(r => {
                                                setCreatedLocations(r)
                                            })
                                    }}
                                    id={c.id}
                                    options={locations}
                                    onAddMore={() => setCounter(prev => prev + 1)}
                                    onValueChange={v => {
                                        setValuesToSend(previous => {
                                            const found = previous.find(prev => prev.id === v.id)
                                            if (!found) return [...previous, v];

                                            return [...previous.filter(f => f.id !== v.id), v]
                                        });
                                    }}
                                />
                        );
                    })}
                    {Array(counter).fill(1).map((_, idx) => {
                        return (
                                <FormCreate
                                    key={idx}
                                    disabled={false}
                                    onDelete={() => {

                                        return clientIatacodes()
                                            .then(r => {
                                                setCreatedLocations(r)
                                            })
                                    }}
                                    onAddMore={() => setCounter(prev => prev + 1)}
                                    options={locations}
                                    onValueChange={v => {
                                        setValuesToSend(previous => {
                                            const found = previous.find(prev => prev.id === v.id)
                                            if (!found) return [...previous, v];
                                            return [...previous.filter(f => f.id !== found.id), v]
                                        });
                                    }}
                                />
                        );
                    })}
            </Grid>
            <Grid item>
                    <Button style={{ width: '100%'}} variant="contained" color="primary" onClick={() => {
                        doPostCode({ data: { codes: valuesToSend } })
                            .then(() => clientIatacodes())
                            .then(r => {
                                setSuccess(true);
                                setCreatedLocations(prev => {
                                    if (prev.length !== r.length)setCounter(0)
                                    return r
                                })
                            })
                    }}>
                        Save
                    </Button>
            </Grid>
        </Grid>
    );
} 