import React, { useState, useEffect } from "react";
import { Button, Checkbox } from '@material-ui/core';
import AxioHook from 'axios-hooks'
import DataTable from 'react-data-table-component';
import { Container, Row, Col } from 'react-bootstrap';
import { getMenus, postRolesForMenu, getRoles } from "../../crud/menu.crud";

const roleChecker = (menuItem, rolesToSend ,setRolesToSend, roleName, forceDisabled) => {
    const checked = rolesToSend.find(r => r.menuItemId === menuItem.id)?.roles.find(r => r.name == roleName)
    return (
        <Checkbox
            disabled={forceDisabled === true}
            defaultChecked={checked ? true : false}
            onChange={() => {
                if (forceDisabled === true) return 
                setRolesToSend(prev => {
                    const clientData = prev.find(p => menuItem.id === p.menuItemId)
                    if (!clientData) return prev

                    const hasRole = clientData.roles.find(r => r.name === roleName)
                    if (hasRole) {
                        clientData.roles = [...clientData.roles.filter(f => f.name !== hasRole.name)]
                        clientData.dirty = true
                    }
                    if (!hasRole) {
                        clientData.roles = [...clientData.roles, { name: roleName }]
                        clientData.dirty = true
                    }

                    return [...prev.filter(p => p.menuItemId !== menuItem.id), clientData];
                })
            }} />
    );
}

const columns = [
    {
        name: 'Menu Title',
        selector: 'title',
        minWidth: "20%"
    },
    {
        name: 'Page to show',
        selector: 'page',
    },
];

export default function Menues() {
    const [dataforTable, setDataForTable] = useState(columns);
    const [rolesToSend, setRolesToSend] = useState([]);
    const [{ data: menues, ...getMenuesReq }, refetchMenues] = AxioHook(getMenus(), { useCache: false })
    const [doPostReq, doPost] = AxioHook(postRolesForMenu(), { manual: true })
    const [rolesReq] = AxioHook(getRoles(), { useCache: false })

    useEffect(() => {
        if (rolesReq.data) setDataForTable(prev => [...prev, ...rolesReq.data])
    }, [rolesReq]);

    useEffect(() => {
        if (menues && rolesToSend.length === 0) {
            setRolesToSend(menues.map(c => ({ dirty: false, menuItemId: c.id, roles: c.roleGuards.map(r => ({ id: r.id, name: r.role })) })))
        }
    }, [menues, getMenuesReq]);

    let Body = (
        <Col span={24}>
            <DataTable
                progressPending={getMenuesReq.loading || rolesReq.loading}
                subHeaderAlign="center"
                title="Roles"
                pagination={true}
                actions={<Button disabled={doPostReq.loading || getMenuesReq.loading} variant="contained" color="primary" onClick={() => {
                    doPost({ data: { roles: rolesToSend.filter(data => data.dirty === true) } })
                    .then(() => refetchMenues())
                    .catch(() => refetchMenues())
                    .then((r) => {
                        setRolesToSend(r.data.map(c => ({ dirty: false, menuItemId: c.id, roles: c.roleGuards.map(r => ({ id: r.id, name: r.role })) })))
                    })
                }}>{doPostReq.loading ? 'Saving...' : 'Save All'}</Button>}
                columns={dataforTable.map(r => {
                    if (!r.role) return r
                    return {
                        name: `${r.role.toUpperCase()}`,
                        cell: (client) => roleChecker(client, rolesToSend,setRolesToSend, r.role, doPostReq.loading) 
                    };
                })}
                data={menues || []}
            />
        </Col>
    );

    if (getMenuesReq.error) {
        Body = (
            <>
                <h3>Error fetching the resource</h3>
            </>
        );
    }

    return (
        <Container fluid>
            <Row>
                {Body}
            </Row>
        </Container>
    );
}
