import React, { useEffect } from 'react';
import { TextField, FormControlLabel, Checkbox, Button } from '@material-ui/core';
import CircularProgress from '@material-ui/core/CircularProgress';
import { postRole } from "../../crud/menu.crud";
import { Formik } from "formik";
import { injectIntl } from "react-intl";
import clsx from "clsx";
import AxioHook from 'axios-hooks'
import { connect } from "react-redux";
import { Modal } from "react-bootstrap";
import { useAppState } from '../AppState';

const CreateRoleModalComponent = ({ handleClose, role, refetch, intl }) => {
  const [, setError] = useAppState('error');

  useEffect(() => refetch, []);

  const [postReq, doPost] = AxioHook(postRole(), { manual: true })

  return (
    <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
      <Modal.Header closeButton>
        <Modal.Title>{role ? `Edit ${role.role}` : "Create Role"}</Modal.Title>
      </Modal.Header>
      <Formik
        initialValues={role|| {
          role: '',
        }}
        validate={values => {
          const errors = {};

          if (!values.role) {
            errors.role = intl.formatMessage({
              id: "AUTH.VALIDATION.REQUIRED_FIELD"
            });
          }


          return errors;
        }}
        onSubmit={(values, { setStatus, setSubmitting }) => {
          setSubmitting(true)

          doPost({ data: values })
            .then(() => {
              setSubmitting(false)
              handleClose();
            })
            .catch(err => {
              setSubmitting(false);
              err.response && setError(err.response.data.error)
              err.response && setStatus(err.response.data.error)
            })
        }}
      >
        {({
          values,
          status,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
        }) => {
          if (postReq.loading) {
            return <CircularProgress />
          }
          return (
            <>
              <form
                noValidate={true}
                autoComplete="off"
                className="kt-form"
                onSubmit={handleSubmit}
              >
                <Modal.Body>
                  {status ? (
                    <div role="alert" className="alert alert-danger">
                      <div className="alert-text">{status}</div>
                    </div>
                  ) : null}

                  <div className="form-group">
                    <TextField
                      type="email"
                      label="Role name"
                      margin="normal"
                      className="kt-width-full"
                      name="role"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.role}
                      helperText={touched.role && errors.role}
                      error={Boolean(touched.role && errors.role)}
                    />
                  </div>

                  <div className="kt-login__actions">

                  </div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="secondary" onClick={() => handleClose('close')}>
                    Close
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    id="kt_login_signin_submit"
                    type="submit"
                    disabled={isSubmitting}
                    className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                      {
                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": isSubmitting
                      }
                    )}`}
                    style={{ paddingRight: postReq.loading ? "3.5rem" : "2.5rem" }}
                  >
                    Submit
                  </Button>
                </Modal.Footer>
              </form>

            </>
          );
        }}
      </Formik>

    </Modal>
  );
}


export const CreateRoleModal = injectIntl(
  connect(
    null,
  )(CreateRoleModalComponent)
);